// * react
import React from 'react';

// ? 
import './AboutMe.css';

// ? 
import photo from './../../images/photo_Dmytro.jpg';

// ?  
import { mainNavigationRouters as routers } from './../../utils/Constants';

// ? 
import LandingBlock from '../LandingBlock/LandingBlock';
import Portfolio from './../Portfolio/Portfolio';

function AboutMe({ serialNumber }) {
  return (
    <LandingBlock
      id='about-me'
      nameOfClass='aboutMe'
      setting={{
        big: true,
        grey: false,
      }}
      title={routers[serialNumber].context}
    >
      <div className='aboutMe__info-img'>
        <div className='aboutMe__info'>
          <h3 className='aboutMe__title'></h3>

          <h4 className='aboutMe__sub-title'>-, 20 </h4>

          <p className='aboutMe__text'>
               .        ,
              .    ,    . 
             .  2022     -  
              .  ,     
            -,   -,    
             .
          </p>

          <a
            lang='en'
            className='aboutMe__link link'
            href='https://github.com/chyVacheck'
            target='_blank'
            rel='noreferrer'
          >
            Github
          </a>
        </div>

        <img className='aboutMe__img' src={photo} alt='  ' />
      </div>

      <Portfolio />
    </LandingBlock>
  );
}

export default AboutMe;
