// ? configs
import configApi from './../config/configApi.json';
import configSite from './../config/configSite.json';
// ? constants
import { status } from './../utils/Constants';

class MainApi {
  constructor(setting) {
    this._adress = setting.baseUrl;
    this._credentials = setting.credentials;
    this._headers = setting.headers;
  }

  //    
  _checkResponse(res, message = '') {
    //   
    if (res.ok) {
      //   dev   
      if (configSite.status === status.dev)
        console.log(
          `   [${this._adress}]${
            message ? '   [' + message + ']' : ''
          }  `,
        );
      return res.json();
    }

    // ? 
    // 429  
    if (res.status === 429) {
      const err = {
        message: res.statusText,
        status: 429,
      };
      //  
      return Promise.reject(err);
    }
    //  
    const error = res.json();
    return error.then((errorObj) =>
      Promise.reject({
        message: errorObj.message,
        status: res.status,
      }),
    );
  }

  //   
  async _request(url, options, message) {
    const res = await fetch(url, options);
    return this._checkResponse(res, message);
  }

  /*  
    user = {
      email: email,
      password: password,
  }*/
  authorization(user) {
    return this._request(
      `${this._adress}/signin`,
      {
        method: 'POST',
        credentials: this._credentials,
        headers: this._headers,
        body: JSON.stringify(user),
      },
      '',
    );
  }

  /*  
    user = {
      name: name,
      email: email,
      password: password,
  }*/
  registration(user) {
    return this._request(
      `${this._adress}/signup`,
      {
        method: 'POST',
        credentials: this._credentials,
        headers: this._headers,
        body: JSON.stringify(user),
      },
      '',
    );
  }

  //    
  getUserInfo() {
    return this._request(
      `${this._adress}/users/me`,
      {
        method: 'GET',
        credentials: this._credentials,
        headers: this._headers,
      },
      '   ',
    );
  }

  /*    
    user = {
      name: name,
      email: email
  }*/
  setUserInfo(user) {
    return this._request(
      `${this._adress}/users/me`,
      {
        method: 'PATCH',
        credentials: this._credentials,
        headers: this._headers,
        body: JSON.stringify({
          name: user.name,
          email: user.email,
        }),
      },
      '   ',
    );
  }

  //   
  logOut() {
    return this._request(
      `${this._adress}/signout`,
      {
        method: 'POST',
        credentials: this._credentials,
        headers: this._headers,
      },
      '  ',
    );
  }

  //  
  validationCookie() {
    return this._request(
      `${this._adress}/users/me`,
      {
        method: 'GET',
        credentials: this._credentials,
        headers: this._headers,
      },
      ' ',
    );
  }

  //   
  getMovies() {
    return this._request(
      `${this._adress}/movies`,
      {
        method: 'GET',
        credentials: this._credentials,
        headers: this._headers,
      },
      '  ',
    );
  }

  //   
  saveMovie(movie) {
    return this._request(
      `${this._adress}/movies`,
      {
        method: 'POST',
        credentials: this._credentials,
        headers: this._headers,
        body: JSON.stringify({
          country: movie.country,
          director: movie.director,
          duration: movie.duration,
          year: movie.year,
          description: movie.description,
          image: movie.image,
          trailerLink: movie.trailerLink,
          thumbnail: movie.thumbnail,
          movieId: movie.movieId,
          nameRU: movie.nameRU,
          nameEN: movie.nameEN,
        }),
      },
      ' ',
    );
  }

  //    
  deleteMovie(id) {
    return this._request(
      `${this._adress}/movies/${id}`,
      {
        method: 'DELETE',
        credentials: this._credentials,
        headers: this._headers,
      },
      '   ',
    );
  }
}

const baseUrl = configApi.Main.BaseUrl[configSite.status];

//   api
const setting = {
  baseUrl: baseUrl,
  credentials: configApi.Main.credentials,
  headers: {
    origin: baseUrl,
    'Content-Type': configApi['Content-Type'],
  },
};

const mainApi = new MainApi(setting);
export default mainApi;
